/**
 * A list of HTML elements tags that should be kept.
 * For nodes that are not listed here, we keep the text only.
 *
 * When adding new elements here, think about their styling in the sidebar (Highlight.css).
 */
const nodesToKeep = [
  'H1',
  'H2',
  'H3',
  'H4',
  'H5',
  'H6',
  'P',
  'DIV',
  'UL',
  'OL',
  'LI',
  'CODE',
];

/**
 * We want to get the content of a range, but keep select elements such as paragraphs, headings, lists, or code blocks
 * to keep and later on be able to display the structure.
 */
export function convertRangeToHtmlString(range: Range): string {
  const rangeFragment = range.cloneContents();
  const div = document.createElement('div');
  div.appendChild(rangeFragment);

  removeNodesAndAttributes(div);
  removeEmptyElements(div);

  // Special handling for lists: if lists are selected, often only the list items are selected.
  // We thus wrap the list items in a list element if all children are list items.
  const containsListItemsOnly =
    div.children.length > 0 &&
    Array.from(div.children).every((child) => child.nodeName === 'LI');

  if (containsListItemsOnly) {
    const listWrapper = document.createElement('ul');
    while (div.firstChild) {
      listWrapper.appendChild(div.firstChild);
    }
    return listWrapper.outerHTML;
  }

  return div.innerHTML;
}

// Recursively traverse all children of the given node.
// On the way, remove all nodes but those we explicitly want to keep and only
// the text for the others.
function removeNodesAndAttributes(node: Node) {
  if (node.nodeType === Node.TEXT_NODE) {
    return;
  }

  if (node.nodeType === Node.COMMENT_NODE) {
    node.parentNode?.removeChild(node);
    return;
  }

  if (node.nodeType === Node.ELEMENT_NODE) {
    const element = node as HTMLElement;

    if (nodesToKeep.includes(element.tagName)) {
      const attributes = element.attributes;

      for (let i = attributes.length - 1; i >= 0; i--) {
        element.removeAttribute(attributes[i].name);
      }
    } else {
      const textNode = document.createTextNode(element.textContent || '');
      element.replaceWith(textNode);
    }
  }

  for (let i = 0; i < node.childNodes.length; i++) {
    removeNodesAndAttributes(node.childNodes[i]);
  }
}

function removeEmptyElements(node: Node) {
  // Traverse the child nodes of the current node
  for (let i = node.childNodes.length - 1; i >= 0; i--) {
    const child = node.childNodes[i];

    removeEmptyElements(child);

    if (
      child.nodeType === Node.ELEMENT_NODE &&
      (!child.textContent || child.textContent.trim() === '')
    ) {
      node.removeChild(child);
    }
  }
}

/**
 * Get a range from a selection. If the selection consists of multiple ranges,
 * they are combined into a single range.
 */
export function getRange(): Range | null {
  const selection = window.getSelection();
  if (selection === null) {
    return null;
  }

  if (selection.isCollapsed) {
    // eslint-disable-next-line no-console
    console.debug('no text selected');

    return null;
  }

  return combineRanges(selection);
}

export const removeSelection = (): void => {
  window.getSelection()?.removeAllRanges();
};

/** Combines multiple ranges from a selection to a single range. */
function combineRanges(selection: Selection): Range | null {
  if (selection.rangeCount === 0) {
    return null;
  }

  const firstRange = selection.getRangeAt(0);
  const lastRange = selection.getRangeAt(selection.rangeCount - 1);

  const combinedRange = document.createRange();
  combinedRange.setStart(firstRange.startContainer, firstRange.startOffset);
  combinedRange.setEnd(lastRange.endContainer, lastRange.endOffset);

  return combinedRange;
}
