import Highlighter from '@highlighter/highlighter';
import {
  ErrorLogPayload,
  type Message,
  MessageType,
} from '@custom-types/message';
import Browser from 'webextension-polyfill';
import ReactDOM, { Root } from 'react-dom/client';
import { Highlight, HighlightElements } from '@custom-types/highlight';
import { HighlightContextMenu } from '@components/context-menu/HighlightContextMenu';
import {
  calculateBoundaries,
  calculateHighlightPopupPosition,
} from '@utils/position';
import { HighlightPopup } from '@components/context-menu/HighlightPopup';
import { ReactNode } from 'react';
import {
  DEFAULT_USER_SETTINGS,
  UserSettings,
} from '@custom-types/userSettings';
import { contentElementStyles } from '@components/context-menu/contentElementStyles';
import { isMobileDevice } from '@utils/mobile';
import { renderToast } from '@utils/toast';
import HighlightSource from '@highlighter/model/source';
import { storeHighlight } from '@utils/highlight-messages';

// @ts-expect-error - no import expected by wxt
export default defineContentScript({
  matches: ['<all_urls>'],
  main,
});

/** The root node to render toast messages. */
export let TOAST_ROOT: Root | null = null;

function main() {
  let userSettings: UserSettings = DEFAULT_USER_SETTINGS;

  const highlightElements: HighlightElements = {
    popups: [],
    contextMenus: [],
  };

  const shadowRootHost = document.createElement('div');
  const shadowRoot = shadowRootHost.attachShadow({ mode: 'open' });
  document.body.appendChild(shadowRootHost);

  TOAST_ROOT = createToastRoot();

  const style = document.createElement('style');
  style.innerHTML = contentElementStyles;
  shadowRoot.appendChild(style);

  setupHighlighter();

  async function setupHighlighter(): Promise<void> {
    await setupSettings();

    const highlighter = new Highlighter();
    highlighter.setColor(userSettings.highlightColorSelection);

    Browser.runtime.onMessage.addListener((message) =>
      messageHandler(message as Message, highlighter)
    );

    highlighter
      .on(Highlighter.event.CREATE, async ({ sources }) => {
        const highlightSource = sources[0];
        await createHighlight(highlightSource);
      })
      .on(Highlighter.event.CLICK, ({ id }, _, e) => {
        e.preventDefault();
        removeHighlightElements(highlightElements);
        const position = calculateBoundaries(highlighter.getElements(id));
        renderHighlightContextMenu(
          highlighter,
          id,
          position.top + window.scrollY,
          position.center + window.scrollX
        );
      })
      .on(Highlighter.event.HOVER, ({ id }) =>
        highlighter.addClass('active', id)
      )
      .on(Highlighter.event.HOVER_OUT, ({ id }) =>
        highlighter.removeClass('active', id)
      );

    await initializeHighlights(highlighter);
    registerEventListenersOnDocument(highlighter);
  }

  async function setupSettings(): Promise<void> {
    userSettings = await Browser.runtime.sendMessage({
      type: MessageType.GetUserSettings,
    });
  }

  async function initializeHighlights(highlighter: Highlighter) {
    const highlights: Highlight[] = await Browser.runtime.sendMessage({
      type: MessageType.GetHighlights,
      payload: window.location.href,
    });

    highlighter.removeAll();
    applyHighlights(highlights, highlighter);
  }

  function registerEventListenersOnDocument(highlighter: Highlighter) {
    if (isMobileDevice()) {
      // We always render the popup below the selection on mobile and need to rerender the popup if the selection is
      // changed via the text selection handles. As changing the selection via the handles doesn't trigger touch events,
      // we listen to selectionchange.
      document.addEventListener('selectionchange', () => {
        // We do not want to close the context menu if we are writing a comment.
        if (isCommentEditorActive()) {
          return;
        }
        removeHighlightElements(highlightElements);
        renderPopupIfNecessary(highlighter);
      });
    } else {
      document.addEventListener('mouseup', (e: MouseEvent) =>
        renderPopupIfNecessary(highlighter, e)
      );
      document.addEventListener('mousedown', () =>
        removeHighlightElements(highlightElements)
      );
    }

    // Remove highlight elements once the user starts typing.
    document.addEventListener('keydown', () => {
      // We do not want to close the context menu if we are writing a comment.
      if (isCommentEditorActive()) {
        return;
      }

      removeHighlightElements(highlightElements);
    });
  }

  function isCommentEditorActive(): boolean {
    const activeElement = shadowRoot.activeElement;

    // We do not want to close the context menu if we are writing a comment.
    return (
      activeElement != null && activeElement instanceof HTMLTextAreaElement
    );
  }

  function renderPopupIfNecessary(
    highlighter: Highlighter,
    event?: MouseEvent
  ): void {
    if (highlightElements.popups.length > 0) {
      return;
    }

    const selection = window.getSelection();
    if (selection == null || selection.isCollapsed) {
      return;
    }

    renderHighlightPopup(highlighter, selection.getRangeAt(0), event);
  }

  function cleanupRangesAndHighlightElements(
    highlightingElements: HighlightElements
  ): void {
    window.getSelection()?.removeAllRanges();
    removeHighlightElements(highlightingElements);
  }

  function removeHighlightElements(
    highlightingElements: HighlightElements
  ): void {
    Object.values(highlightingElements)
      .flat()
      .forEach((highlightingElement) => {
        highlightingElement.removeEventListener('mousedown', stopPropagation);
        highlightingElement.remove();
      });

    highlightingElements.popups.length = 0;
    highlightingElements.contextMenus.length = 0;
  }

  function renderHighlightPopup(
    highlighter: Highlighter,
    selectedRange: Range,
    event?: MouseEvent
  ): void {
    if (userSettings.autoHighlighting || userSettings.highlightingDisabled) {
      return;
    }

    const highlightPopupWidth = userSettings.extendedHighlighting ? 260 : 190;
    const highlightPopupHeight = 45;
    const { top, left } = calculateHighlightPopupPosition(
      selectedRange,
      highlightPopupWidth,
      highlightPopupHeight,
      event
    );
    const highlightPopupContainer = renderHighlightComponent(
      <HighlightPopup
        highlighter={highlighter}
        top={top}
        left={left}
        onHighlight={() => cleanupRangesAndHighlightElements(highlightElements)}
        extendedHighlighting={userSettings.extendedHighlighting}
      />
    );
    highlightElements.popups.push(highlightPopupContainer);
  }

  function renderHighlightContextMenu(
    highlighter: Highlighter,
    highlightID: string,
    top: number,
    left: number
  ): void {
    const contextMenuContainer = renderHighlightComponent(
      <HighlightContextMenu
        highlighter={highlighter}
        highlightID={highlightID}
        top={top}
        left={left}
        onClick={() => cleanupRangesAndHighlightElements(highlightElements)}
      />
    );

    highlightElements.contextMenus.push(contextMenuContainer);
  }

  function renderHighlightComponent(component: ReactNode): HTMLDivElement {
    const buttonContainer = document.createElement('div');
    buttonContainer.addEventListener('mousedown', stopPropagation);
    shadowRoot.appendChild(buttonContainer);
    ReactDOM.createRoot(buttonContainer).render(component);

    return buttonContainer;
  }

  function createToastRoot(): ReactDOM.Root {
    const toastContainer = document.createElement('div');
    shadowRoot.appendChild(toastContainer);
    return ReactDOM.createRoot(toastContainer);
  }

  function createHighlight(highlightSource: HighlightSource): Promise<void> {
    const createdAt = new Date().toISOString();

    const highlight = {
      url: location.href,
      exported: false,
      createdAt,
      updatedAt: createdAt,
      startMeta: highlightSource.startMeta,
      endMeta: highlightSource.endMeta,
      text: highlightSource.text,
      title: document.title,
      color: highlightSource.color,
      markdown: highlightSource.markdown,
      id: highlightSource.id,
      hidden: false,
    };

    return storeHighlight(highlight);
  }

  function stopPropagation(event: MouseEvent): void {
    event.stopPropagation();
  }

  function applyHighlights(
    highlights: Highlight[],
    highlighter: Highlighter
  ): void {
    highlights.forEach((highlight) => {
      if (!highlight.hidden) {
        highlighter.fromStore(
          highlight.startMeta,
          highlight.endMeta,
          highlight.text,
          highlight.id,
          highlight.color,
          highlight.markdown
        );
      }
    });
  }

  async function messageHandler(
    message: Message,
    highlighter: Highlighter
  ): Promise<void> {
    switch (message.type) {
      case MessageType.DeleteFromHighlighter:
        return highlighter.remove(message.payload as string);
      case MessageType.TabUpdate:
        highlighter.removeAll();
        const highlights = message.payload as Highlight[];
        applyHighlights(highlights, highlighter);
        break;
      case MessageType.UserSettingsUpdate:
        await handleUserSettingsUpdate(message, highlighter);
        break;
      case MessageType.Error:
        const { title, description } = message.payload as ErrorLogPayload;
        renderToast(title, description);
        break;
      default:
        console.error(
          'Message type ',
          message.type,
          ' not supported in content script'
        );
    }
  }

  async function handleUserSettingsUpdate(
    message: Message,
    highlighter: Highlighter
  ) {
    const updatedUserSettings = message.payload as UserSettings;

    // Remove all highlights if highlighting has been disabled and make sure that auto highlight is disabled.
    if (updatedUserSettings.highlightingDisabled) {
      highlighter.removeAll();
      highlighter.stop();
    }

    // Re-enable highlighting if it has been enabled.
    if (
      userSettings.highlightingDisabled &&
      !updatedUserSettings.highlightingDisabled
    ) {
      if (updatedUserSettings.autoHighlighting) {
        highlighter.run();
      }

      await initializeHighlights(highlighter);
    }

    highlighter.stop();
    if (updatedUserSettings.autoHighlighting) {
      highlighter.run();
    }
    highlighter.setColor(updatedUserSettings.highlightColorSelection);
    userSettings = updatedUserSettings;
  }
}
