import { Toast } from '@components/context-menu/Toast';
import { TOAST_ROOT } from '../entrypoints/content';

/** Render a toast message. */
export function renderToast(title: string, description: string): void {
  if (TOAST_ROOT == null) {
    return;
  }

  TOAST_ROOT.render(
    <Toast key={Math.random()} title={title} description={description} />
  );
}
