/**
 * adapter for mobile and desktop events
 */

import type { IInteraction } from '@highlighter/types';
import { UserInputEvent } from '@highlighter/types';
import { isMobileDevice } from '@utils/mobile';

export default (): IInteraction => {
  const isMobile = isMobileDevice();

  return {
    PointerEnd: isMobile ? UserInputEvent.touchend : UserInputEvent.mouseup,
    // On mobile, we use touchend to successfully intercept selectionchange
    // events that are triggered when tapping the context menu
    PointerTap: isMobile ? UserInputEvent.touchend : UserInputEvent.click,
    // hover and click will be the same event in mobile
    PointerOver: isMobile
      ? UserInputEvent.touchstart
      : UserInputEvent.mouseover,
  };
};
