import {
  DomMeta,
  HookMap,
  HighlighterOptions,
  FormatTagName,
} from '@highlighter/types';
import EventEmitter from '@highlighter/util/event.emitter';
import HighlightRange from '@highlighter/model/range';
import HighlightSource from '@highlighter/model/source';
import Hook from '@highlighter/util/hook';
import getInteraction from '@highlighter/util/interaction';
import Cache from '@highlighter/data/cache';
import Painter from '@highlighter/painter';
import {
  eventEmitter,
  getDefaultOptions,
  INTERNAL_ERROR_EVENT,
} from '@highlighter/util/const';
import { ERROR, EventType, CreateFrom } from '@highlighter/types';
import {
  addClass,
  removeClass,
  isHighlightWrapNode,
  getHighlightById,
  getHighlightsByRoot,
  getHighlightId,
  addEventListener,
  removeEventListener,
} from '@highlighter/util/dom';
import { Color } from '@highlighter/util/color';
import TurndownService from 'turndown';

interface EventHandlerMap {
  [key: string]: (...args: any[]) => void;
  [EventType.CLICK]: (
    data: { id: string },
    h: Highlighter,
    e: MouseEvent | TouchEvent
  ) => void;
  [EventType.HOVER]: (
    data: { id: string },
    h: Highlighter,
    e: MouseEvent | TouchEvent
  ) => void;
  [EventType.HOVER_OUT]: (
    data: { id: string },
    h: Highlighter,
    e: MouseEvent | TouchEvent
  ) => void;
  [EventType.CREATE]: (
    data: { sources: HighlightSource[]; type: CreateFrom },
    h: Highlighter
  ) => void;
  [EventType.REMOVE]: (data: { ids: string[] }, h: Highlighter) => void;
}

export default class Highlighter extends EventEmitter<EventHandlerMap> {
  static event = EventType;

  static isHighlightWrapNode = isHighlightWrapNode;

  hooks: HookMap;

  painter?: Painter;

  cache: Cache;

  private hoverId: string | null = null;

  private options: HighlighterOptions;

  private readonly event = getInteraction();

  private readonly turndownService: TurndownService;

  constructor(options?: HighlighterOptions) {
    super();
    this.options = getDefaultOptions();
    this.hooks = this._getHooks();
    this.setOption(options);
    this.cache = new Cache();
    this.turndownService = new TurndownService({ headingStyle: 'atx' });

    const $root = this.options.$root;

    addEventListener($root, this.event.PointerOver, this._handleHighlightHover);
    addEventListener($root, this.event.PointerTap, this._handleHighlightClick);
    eventEmitter.on(INTERNAL_ERROR_EVENT, this._handleError);
  }

  // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
  static isHighlightSource = (d: any) => !!d.__isHighlightSource;

  run = () =>
    addEventListener(
      this.options.$root,
      this.event.PointerEnd,
      this.handleSelection
    );

  createFromSelection = (formatterTag?: FormatTagName) => {
    return this.handleSelectionWithOptionalFormat(formatterTag);
  };

  stop = () => {
    removeEventListener(
      this.options.$root,
      this.event.PointerEnd,
      this.handleSelection
    );
  };

  addClass = (className: string, id?: string) => {
    this.getElements(id).forEach(($n) => {
      addClass($n, className);
    });
  };

  removeClass = (className: string, id?: string) => {
    this.getElements(id).forEach(($n) => {
      removeClass($n, className);
    });
  };

  getElements = (id?: string): HTMLElement[] =>
    id
      ? getHighlightById(this.options.$root, id, this.options.wrapTag)
      : getHighlightsByRoot(this.options.$root, this.options.wrapTag);

  dispose = () => {
    const $root = this.options.$root;

    removeEventListener(
      $root,
      this.event.PointerOver,
      this._handleHighlightHover
    );
    removeEventListener($root, this.event.PointerEnd, this.handleSelection);
    removeEventListener(
      $root,
      this.event.PointerTap,
      this._handleHighlightClick
    );
    this.removeAll();
  };

  setOption = (options?: HighlighterOptions) => {
    this.options = {
      ...this.options,
      ...options,
    };
    this.painter = new Painter(
      {
        $root: this.options.$root,
        wrapTag: this.options.wrapTag,
        className: this.options.style.className,
        exceptSelectors: this.options.exceptSelectors,
        color: this.options.style.color,
      },
      this.hooks
    );
  };

  fromStore = (
    start: DomMeta,
    end: DomMeta,
    text: string,
    id: string,
    color: Color,
    markdown: string,
    extra?: unknown
  ): HighlightSource | null => {
    const highlightSource = new HighlightSource(
      start,
      end,
      text,
      id,
      color,
      markdown,
      extra
    );

    try {
      this._highlightFromHSource(highlightSource);

      return highlightSource;
    } catch (err: unknown) {
      eventEmitter.emit(INTERNAL_ERROR_EVENT, {
        type: ERROR.HIGHLIGHT_SOURCE_RECREATE,
        error: err,
        detail: highlightSource,
      });

      return null;
    }
  };

  remove(id: string) {
    if (!id) {
      return;
    }

    const doseExist = this.painter?.removeHighlight(id);

    this.cache.remove(id);

    // only emit REMOVE event when highlight exist
    if (doseExist) {
      this.emit(EventType.REMOVE, { ids: [id] }, this);
    }
  }

  get(id: string): HighlightSource | undefined {
    return this.cache.get(id);
  }

  removeAll() {
    this.painter?.removeAllHighlight();

    const ids = this.cache.removeAll();

    this.emit(EventType.REMOVE, { ids }, this);
  }

  setColor(color: Color) {
    this.painter?.setColor(color);
  }

  getColor(): Color {
    return this.painter?.getColor() ?? this.options.style.color;
  }

  private readonly _getHooks = (): HookMap => ({
    Render: {
      UUID: new Hook('Render.UUID'),
      SelectedNodes: new Hook('Render.SelectedNodes'),
      WrapNode: new Hook('Render.WrapNode'),
    },
    Serialize: {
      Restore: new Hook('Serialize.Restore'),
      RecordInfo: new Hook('Serialize.RecordInfo'),
    },
    Remove: {
      UpdateNodes: new Hook('Remove.UpdateNodes'),
    },
  });

  private readonly _highlightFromHRange = (
    range: HighlightRange
  ): HighlightSource | null => {
    const source: HighlightSource = range.serialize(
      this.options.$root,
      this.hooks,
      this.painter!.getColor()
    );
    const $wraps = this.painter?.highlightRange(range);

    if ($wraps?.length === 0) {
      eventEmitter.emit(INTERNAL_ERROR_EVENT, {
        type: ERROR.DOM_SELECTION_EMPTY,
      });

      return null;
    }

    this.cache.save(source);
    this.emit(
      EventType.CREATE,
      { sources: [source], type: CreateFrom.INPUT },
      this
    );

    return source;
  };

  private _highlightFromHSource(
    sources: HighlightSource | HighlightSource[] = []
  ) {
    this.painter?.highlightSource(sources);
    this.cache.save(sources);
  }

  private readonly handleSelection = () =>
    this.handleSelectionWithOptionalFormat();

  private readonly handleSelectionWithOptionalFormat = (
    formatterTag?: FormatTagName
  ) => {
    const range = HighlightRange.fromSelection(
      this.hooks.Render.UUID,
      this.turndownService,
      formatterTag
    );

    if (range) {
      this._highlightFromHRange(range);
      HighlightRange.removeDomRange();
    }
  };

  private readonly _handleHighlightHover = (e: MouseEvent | TouchEvent) => {
    const $target = e.target as HTMLElement;

    if (!isHighlightWrapNode($target)) {
      this.hoverId &&
        this.emit(EventType.HOVER_OUT, { id: this.hoverId }, this, e);
      this.hoverId = null;

      return;
    }

    const id = getHighlightId($target, this.options.$root);

    // prevent trigger in the same highlight range
    if (this.hoverId === id) {
      return;
    }

    // hover another highlight range, need to trigger previous highlight hover out event
    if (this.hoverId) {
      this.emit(EventType.HOVER_OUT, { id: this.hoverId }, this, e);
    }

    this.hoverId = id;
    this.emit(EventType.HOVER, { id: this.hoverId }, this, e);
  };

  private readonly _handleError = (type: {
    type: ERROR;
    detail?: HighlightSource;
    error?: any;
  }) => {
    if (this.options.verbose) {
      // eslint-disable-next-line no-console
      console.warn(type);
    }
  };

  private readonly _handleHighlightClick = (e: MouseEvent | TouchEvent) => {
    const $target = e.target as HTMLElement;

    if (isHighlightWrapNode($target)) {
      const id = getHighlightId($target, this.options.$root);

      this.emit(EventType.CLICK, { id }, this, e);
    }
  };
}
