import { Color } from '@highlighter/util/color';
import { ReactElement } from 'react';

type ColorProps = {
  onChange: (color: Color) => void;
  color: Color;
};

/** A single color that can be selected for highlighting. */
export function ColorSwatch({ color, onChange }: ColorProps): ReactElement {
  return (
    <button
      className="color-swatch shadow"
      aria-label={color}
      onClick={() => onChange(color)}
      style={{ backgroundColor: color }}
      type="button"
    />
  );
}
