import { JSX, useState } from 'react';
import Highlighter from '@highlighter/highlighter';
import { ContextMenuCommentEditor } from '@components/context-menu/comment/ContextMenuCommentEditor';
import { ActionMenu } from '@components/context-menu/actions/ActionMenu';

type HighlightSelectionMenuProps = {
  highlighter: Highlighter;
  highlightID: string;
  top: number;
  left: number;
  onClick: () => void;
};

/**
 * A menu bar that is shown when a highlight is selected.
 */
export function HighlightContextMenu({
  highlighter,
  highlightID,
  top,
  left,
  onClick,
}: HighlightSelectionMenuProps): JSX.Element {
  const [showCommentSection, setShowCommentSection] = useState(false);

  if (showCommentSection) {
    return (
      <ContextMenuCommentEditor
        highlightID={highlightID}
        onClose={() => setShowCommentSection(false)}
        top={top}
        left={left}
      />
    );
  }

  return (
    <ActionMenu
      top={top}
      left={left}
      onClick={onClick}
      highlighter={highlighter}
      highlightID={highlightID}
      setShowCommentSection={setShowCommentSection}
    />
  );
}
