import Highlighter from '@highlighter/highlighter';
import { JSX, useState } from 'react';
import { HighlightButton } from '@components/context-menu/HighlightButton';
import { Color } from '@highlighter/util/color';
import { renderToast } from '@utils/toast';
import { OpenColorSelectionButton } from '@components/context-menu/color-selection/OpenColorSelectionButton';
import { Divider } from '@components/context-menu/commons/Divider';
import { Actions } from '@components/context-menu/actions/Actions';
import { Action } from '@components/context-menu/actions/Action';
import { ColorPicker } from '@components/context-menu/color-selection/ColorPicker';
import {
  IconCode,
  IconList,
  IconH1,
  IconH2,
  IconH3,
} from '@tabler/icons-react';
import { FormatTagName } from '@highlighter/types';
import Browser from 'webextension-polyfill';
import { MessageType } from '@custom-types/message';
import { UserSettings } from '@custom-types/userSettings';

type HighlightButtonProps = {
  highlighter: Highlighter;
  top: number;
  left: number;
  onHighlight: () => void;
  extendedHighlighting: boolean;
};

/**
 * A popup to highlight text and select the highlighting color.
 */
export function HighlightPopup({
  highlighter,
  top,
  left,
  onHighlight,
  extendedHighlighting,
}: HighlightButtonProps): JSX.Element {
  const [selectedColor, setSelectedColor] = useState<Color>(
    highlighter.getColor()
  );
  const [colorSelectionOpen, setColorSelectionOpen] = useState(false);

  // TODO: Create and store highlights here directly instead of going through the indirection of the highlighter.
  const createHighlight = (formatterTag?: FormatTagName) => {
    try {
      highlighter.createFromSelection(formatterTag);
    } catch (e) {
      renderToast('Highlight not created.', (e as Error).message);
    }

    onHighlight();
  };

  const onColorSelection = async (color: Color) => {
    setSelectedColor(color);
    setColorSelectionOpen(false);
    const userSettings: UserSettings = await Browser.runtime.sendMessage({
      type: MessageType.GetUserSettings,
    });

    userSettings.highlightColorSelection = color;
    await Browser.runtime.sendMessage({
      type: MessageType.UpdateUserSettings,
      payload: userSettings,
    });
  };

  return (
    <div className="highlight-popup" style={{ top, left }}>
      <HighlightButton onClick={createHighlight} />
      <Actions>
        <Action as="div" className="color-selection-section">
          {colorSelectionOpen ? (
            <ColorPicker onChange={onColorSelection} />
          ) : null}
          <OpenColorSelectionButton
            onClick={() =>
              setColorSelectionOpen((prevSelection) => !prevSelection)
            }
            selectedColor={selectedColor}
          />
        </Action>
        {extendedHighlighting ? (
          <>
            <Divider />
            <Action onClick={() => createHighlight('li')}>
              <IconList size={24} />
            </Action>
            <Divider />
            <Action onClick={() => createHighlight('code')}>
              <IconCode size={24} />
            </Action>
            <Divider />
            <Action onClick={() => createHighlight('h1')}>
              <IconH1 size={24} />
            </Action>
            <Divider />
            <Action onClick={() => createHighlight('h2')}>
              <IconH2 size={24} />
            </Action>
            <Divider />
            <Action onClick={() => createHighlight('h3')}>
              <IconH3 size={24} />
            </Action>{' '}
          </>
        ) : null}
      </Actions>
    </div>
  );
}
