import { ReactElement } from 'react';
import { IconCopy, IconEyeOff, IconNote, IconTrash } from '@tabler/icons-react';
import { copyToClipboard } from '@utils/clipboard';
import Highlighter from '@highlighter/highlighter';
import {
  deleteHighlight,
  fetchHighlight,
  updateHighlight,
} from '@utils/highlight-messages';

type ActionMenuProps = {
  highlighter: Highlighter;
  top: number;
  left: number;
  highlightID: string;
  onClick: () => void;
  setShowCommentSection: (
    value: ((prevState: boolean) => boolean) | boolean
  ) => void;
};

/** A section of the context menu with different actions to interact with an existing highlight. */
export function ActionMenu({
  highlighter,
  top,
  left,
  highlightID,
  onClick,
  setShowCommentSection,
}: ActionMenuProps): ReactElement {
  const onDeleteHighlight = async (highlightID: string) => {
    highlighter.remove(highlightID);
    await deleteHighlight(highlightID);
    onClick();
  };

  const copyHighlightText = async () => {
    const highlight = highlighter.get(highlightID);
    if (highlight !== undefined) {
      await copyToClipboard(highlight.text);
    }
    onClick();
  };

  const hideHighlight = async () => {
    const highlight = await fetchHighlight(highlightID);
    highlight.hidden = true;
    await updateHighlight(highlight);
    highlighter.remove(highlightID);
    onClick();
  };

  const iconSize = 24;
  const contextMenuHeightAndMargin = 45;
  const contextMenuWidth = 140;
  return (
    <div
      className="highlight-menu-bar shadow"
      style={{
        top: top - contextMenuHeightAndMargin,
        left: left - contextMenuWidth / 2,
      }}
    >
      <button
        onClick={() => onDeleteHighlight(highlightID)}
        type="button"
        title="delete"
      >
        <IconTrash size={iconSize} />
      </button>
      <div className="divider" />
      <button onClick={copyHighlightText} type="button" title="copy">
        <IconCopy size={iconSize} />
      </button>
      <div className="divider" />
      <button onClick={hideHighlight} type="button" title="hide">
        <IconEyeOff />
      </button>
      <div className="divider" />
      <button
        onClick={() => setShowCommentSection(true)}
        type="button"
        title="comment"
      >
        <IconNote size={iconSize} />
      </button>
    </div>
  );
}
