import { Color, ColorKey } from '@highlighter/util/color';
import { ColorSwatch } from '@components/context-menu/color-selection/ColorSwatch';
import { ReactElement } from 'react';

type ColorPickerProps = {
  onChange: (color: Color) => void;
};

/** Color picker to select colors for highlighting. */
export function ColorPicker({ onChange }: ColorPickerProps): ReactElement {
  const colorKeys: ColorKey[] = Object.keys(Color) as ColorKey[];

  return (
    <div className="color-picker">
      {colorKeys.map((color) => (
        <ColorSwatch
          key={color}
          aria-label={color}
          color={Color[color]}
          onChange={onChange}
        />
      ))}
    </div>
  );
}
