import { JSX, MouseEvent, TouchEvent } from 'react';
import { isMobileDevice } from '@utils/mobile';
import { IconHighlight } from '@tabler/icons-react';

type HighlightButtonProps = {
  onClick: () => void;
};

/**
 * A button to highlight text.
 */
export function HighlightButton({
  onClick,
}: HighlightButtonProps): JSX.Element {
  const withStopPropagation = (event: MouseEvent | TouchEvent) => {
    event.stopPropagation();
    onClick();
  };

  const isMobile = isMobileDevice();

  // We currently need the touchstart event on Firefox for Android as the onClick is not
  // triggered after extending selections with the selection handles.
  return (
    <button
      className="highlight-button shadow"
      onClick={isMobile ? (e) => e.stopPropagation() : withStopPropagation}
      onTouchStart={withStopPropagation}
      type="button"
    >
      <IconHighlight size={24} />
    </button>
  );
}
