/**
 * HighlightSource Class (HSource)
 * This Object can be deSerialized to HRange.
 * Also, it has the ability for persistence.
 */

import type { DomMeta, DomNode, HookMap } from '@highlighter/types';
import HighlightRange from '@highlighter/model/range/index';
import {
  getTextChildByOffset,
  queryElementNode,
} from '@highlighter/model/source/dom';
import { Color } from '@highlighter/util/color';

class HighlightSource {
  startMeta: DomMeta;

  endMeta: DomMeta;

  text: string;

  markdown: string;

  id: string;

  color: Color;

  extra?: unknown;

  __isHighlightSource: unknown;

  constructor(
    startMeta: DomMeta,
    endMeta: DomMeta,
    text: string,
    id: string,
    color: Color,
    markdown: string,
    extra?: unknown
  ) {
    this.startMeta = startMeta;
    this.endMeta = endMeta;
    this.text = text;
    this.id = id;
    this.color = color;
    this.markdown = markdown;
    this.__isHighlightSource = {};

    if (extra) {
      this.extra = extra;
    }
  }

  deSerialize($root: Document | HTMLElement, hooks: HookMap): HighlightRange {
    const { start, end } = queryElementNode(this, $root);
    let startInfo = getTextChildByOffset(start, this.startMeta.textOffset);
    let endInfo = getTextChildByOffset(end, this.endMeta.textOffset);

    if (!hooks.Serialize.Restore.isEmpty()) {
      const res: DomNode[] =
        hooks.Serialize.Restore.call(this, startInfo, endInfo) || [];

      startInfo = res[0] || startInfo;
      endInfo = res[1] || endInfo;
    }

    return new HighlightRange(
      startInfo,
      endInfo,
      this.text,
      this.id,
      this.markdown,
      true
    );
  }
}

export default HighlightSource;
