/**
 * the HighlightRange Class（HRange）
 * It's a special object called HRange in Highlighter,
 * represents for a piece of chosen dom
 */

import { DomNode, FormatTagName, HookMap } from '@highlighter/types';
import type Hook from '@highlighter/util/hook';
import HighlightSource from '@highlighter/model/source/index';
import { ERROR } from '@highlighter/types';
import {
  getRange,
  convertRangeToHtmlString,
  removeSelection,
} from '@highlighter/model/range/selection';
import uuid from '@highlighter/util/uuid';
import { getDomMeta, formatDomNode } from '@highlighter/model/range/dom';
import { eventEmitter, INTERNAL_ERROR_EVENT } from '@highlighter/util/const';
import { Color } from '@highlighter/util/color';
import TurndownService from 'turndown';

class HighlightRange {
  static removeDomRange = removeSelection;

  start: DomNode;

  end: DomNode;

  text: string;

  markdown: string;

  id: string;

  frozen: boolean;

  constructor(
    start: DomNode,
    end: DomNode,
    text: string,
    id: string,
    markdown: string,
    frozen = false
  ) {
    if (start.$node.nodeType !== 3 || end.$node.nodeType !== 3) {
      eventEmitter.emit(INTERNAL_ERROR_EVENT, {
        type: ERROR.RANGE_NODE_INVALID,
      });
    }

    this.start = formatDomNode(start);
    this.end = formatDomNode(end);
    this.text = text;
    this.frozen = frozen;
    this.id = id;
    this.markdown = markdown;
  }

  static fromSelection(
    idHook: Hook<string>,
    turndownService: TurndownService,
    formatterTag?: FormatTagName
  ) {
    const range = getRange();

    if (!range) {
      return null;
    }

    const start: DomNode = {
      $node: range.startContainer,
      offset: range.startOffset,
    };
    const end: DomNode = {
      $node: range.endContainer,
      offset: range.endOffset,
    };

    const text = range.toString();
    let htmlString: string;
    if (formatterTag !== undefined) {
      htmlString = `<${formatterTag}>${text}</${formatterTag}>`;
    } else {
      htmlString = convertRangeToHtmlString(range);
    }

    let id = idHook.call(start, end, text);

    id = typeof id !== 'undefined' && id !== null ? id : uuid();

    return new HighlightRange(
      start,
      end,
      text,
      id,
      turndownService.turndown(htmlString)
    );
  }

  // serialize the HRange instance
  // so that you can save the returned object (e.g. use JSON.stringify on it and send to backend)
  serialize(
    $root: Document | HTMLElement,
    hooks: HookMap,
    color: Color
  ): HighlightSource {
    const startMeta = getDomMeta(
      this.start.$node as Text,
      this.start.offset,
      $root
    );
    const endMeta = getDomMeta(this.end.$node as Text, this.end.offset, $root);

    let extra;

    if (!hooks.Serialize.RecordInfo.isEmpty()) {
      extra = hooks.Serialize.RecordInfo.call(this.start, this.end, $root);
    }

    this.frozen = true;

    return new HighlightSource(
      startMeta,
      endMeta,
      this.text,
      this.id,
      color,
      this.markdown,
      extra
    );
  }
}

export default HighlightRange;
