import { ReactElement, useEffect, useState } from 'react';
import { TextArea } from '@components/context-menu/comment/TextArea';
import { Message, MessageType } from '@custom-types/message';
import { Highlight } from '@custom-types/highlight';
import Browser from 'webextension-polyfill';
import { IconDeviceFloppy } from '@tabler/icons-react';
import { fetchHighlight, updateHighlight } from '@utils/highlight-messages';

type CommentEditorProps = {
  highlightID: string;
  onClose: () => void;
  top: number;
  left: number;
};

/** A section of the context menu to write or edit a comment for a highlight. */
export function ContextMenuCommentEditor({
  highlightID,
  onClose,
  top,
  left,
}: CommentEditorProps): ReactElement {
  const [comment, setComment] = useState('');
  const [initialComment, setInitialComment] = useState('');

  const updateComment = async (): Promise<void> => {
    const highlight: Highlight = await fetchHighlight(highlightID);

    if (comment.trim() === '') {
      highlight.comment = undefined;
    } else {
      highlight.comment = comment.trim();
    }

    await updateHighlight(highlight);

    onClose();
  };

  useEffect(() => {
    const fetchHighlight = async () => {
      const message: Message = {
        type: MessageType.GetHighlight,
        payload: highlightID,
      };
      const highlight: Highlight = await Browser.runtime.sendMessage(message);
      setComment(highlight.comment ?? '');
      setInitialComment(highlight.comment ?? '');
    };

    fetchHighlight();
  }, [highlightID]);

  // height and width need to match the css values in css class "comment-editor"
  const contextMenuHeightAndMargin = 45;
  const contextMenuWidth = 275;
  const iconSize = 24;

  return (
    <div
      className="comment-editor"
      style={{
        top: top - contextMenuHeightAndMargin,
        left: left - contextMenuWidth / 2,
      }}
    >
      <TextArea onChange={(comment) => setComment(comment)} value={comment} />
      {initialComment.trim() !== comment.trim() ? (
        <div className="save-button-container">
          <button onClick={updateComment} type="button" title="save">
            <IconDeviceFloppy size={iconSize} />
          </button>
        </div>
      ) : null}
    </div>
  );
}
