import { ReactElement } from 'react';

type TextAreaProps = {
  onChange: (comment: string) => void;
  value: string;
};

/** Text area to create comments. */
export function TextArea({ onChange, value }: TextAreaProps): ReactElement {
  return (
    <textarea
      aria-label="comment-text"
      className="comment-text-area"
      value={value}
      onChange={(e) => {
        e.stopPropagation();
        onChange(e.target.value);
      }}
      autoFocus
    />
  );
}
