/** CSS styles for the highlight popup. */
const highlightStyles: string = `
.highlight-popup {
    position: absolute;
    z-index: 9999;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}

.actions {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    background-color: #a8a8a8;
    height: 35px;
    align-items: center;
}

.highlight-button {
    background-color: #828282;
    color: #fff;
    border: none;
    text-align: center;
    text-decoration: none;
    height: 45px;
    width: 45px;
    border-radius: 50%;
    padding: .5rem;
    transition: background-color 0.3s ease;
    z-index: 10000;
}

.highlight-button:hover, button.action:hover {
    background-color: #696969;
}

.color-selection-section {
    height: 35px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.open-color-selection-button {
    text-decoration: none;
    border: none;
    width: 25px;
    height: 25px;
    margin-right: 5px;
    border-radius: 5px;
    box-shadow: inset 0 0 2px #696969;
}

.open-color-selection-button:hover {
    filter: brightness(90%);
}

.color-picker {
    position: absolute;
    display: flex;
    flex-wrap: nowrap;
    bottom: 100%;
    left: 50px;
}

.color-swatch {
    width: 25px;
    height: 25px;
    border-radius: 5px;
    margin-right: 5px;
    border: none;
    text-decoration: none;
}

.action {
    color: #fff;
    height: 100%;
    background-color: transparent;
    border: none;
    width: 35px;
}

.action:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.action:first-child {
    width: 57px;
    margin-left: -22px;
    padding-left: 22px;
    background-color: #a8a8a8;
}

.radius-right {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
`;

/** CSS styles for the menu bar. */
const menuBarStyles: string = `
.highlight-menu-bar button:hover {
  background-color: #696969;
}

.highlight-menu-bar {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  z-index: 9999;
  background-color: #828282;
  height: 35px;
  border-radius: 5px;
}

.highlight-menu-bar button {
  color: #fff;
  height: 100%;
  background-color: transparent;
  border: none;
  width: 35px;
  border-radius: 5px;
}
`;

/** CSS styles for the Toast. */
const toastStyles: string = `
#toast {
    visibility: hidden;
    width: 250px;
    margin-left: -125px;
    background-color: #2e2e2e;
    color: #fff;
    border-radius: 5px;
    padding: 12px;
    position: fixed;
    z-index: 10000;
    left: 50%;
    bottom: 30px;
    border-left: red 5px solid;
    line-height: 1.5;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: row;
}

#toast .close-button {
    background: none;
    color: #fff;
    border: none;
}

#toast.show {
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.6s 4.5s;
    animation: fadein 0.5s, fadeout 0.6s 4.5s;
}

#toast h3 {
    margin: 0 0 6px 0;
    font-weight: normal;
   
}

#toast p {
    margin: 0;
    color: #828282;
}

@-webkit-keyframes fadein {
    from {bottom: 0; opacity: 0;}
    to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
    from {bottom: 0; opacity: 0;}
    to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
    from {bottom: 30px; opacity: 1;}
    to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
    from {bottom: 30px; opacity: 1;}
    to {bottom: 0; opacity: 0;}
}
`;

const commentEditorStyles: string = `
.comment-editor {
    position: absolute;
    z-index: 9999;
    min-width: 275px;
    display: flex;
    background-color: transparent;

    & > textarea {
        background-color: #2e2e2e;
        color: #c9c9c9;
        transition: border-color 100ms ease;
        appearance: none;
        border-top-left-radius: 0.25rem;
        border-bottom-left-radius: 0.25rem;
        border: 2px solid #424242;
        outline: none;
        cursor: text;
        overflow: hidden;
        flex: 1;

        &:focus {
            border-color: #0c8599;
        }
    }

    .save-button-container {
        display: flex;
        justify-content: center;
        align-items: center;

        & > button {
            border: none;
            border-top-right-radius: 0.25rem;
            border-bottom-right-radius: 0.25rem;
            color: #0c8599;
            background-color: #2a4044;
            width: 38px;
            height: 38px;

            &:hover,:focus {
                color: #66d9e8;
            }

            &:focus {
                outline: none;
            }
        }
    }
}
`;

/** CSS styles for common elements. */
const commonStyles: string = `    
button {
  cursor: pointer;
}

.shadow {
  box-shadow: 0 3px 5px -1px rgb(0 0 0 / 20%), 0 6px 10px 0 rgb(0 0 0 / 14%), 0 1px 18px 0 rgb(0 0 0 / 12%);
}

.divider {
  height: 80%;
  width: 1px;
  background-color: #fff;
}
`;

/**
 * All the CSS styles used in elements rendered by the content script.
 * This CSS is used to inject the styles into the page via a shadow DOM.
 */
export const contentElementStyles =
  highlightStyles +
  menuBarStyles +
  toastStyles +
  commentEditorStyles +
  commonStyles;
