import { Color } from '@highlighter/util/color';
import { ReactElement } from 'react';

type OpenColorSelectionButtonProps = {
  onClick: () => void;
  selectedColor: Color;
};

/**
 * A button to open the color selection.
 */
export function OpenColorSelectionButton({
  onClick,
  selectedColor,
}: OpenColorSelectionButtonProps): ReactElement {
  return (
    <button
      onClick={onClick}
      aria-label="open color selection"
      className="open-color-selection-button"
      style={{ backgroundColor: selectedColor }}
      type="button"
    />
  );
}
