/**
 * Calculate the position of the highlight popup based on a mouse or touch event.
 * Considers the scroll position and the window size.
 */
export function calculateHighlightPopupPosition(
  selectedRange: Range,
  popupWidth: number,
  popupHeight: number,
  event?: MouseEvent
): { top: number; left: number } {
  if (event === undefined) {
    return calculateMobileHighlightPopupPosition(selectedRange, popupWidth);
  }

  return calculateDesktopHighlightPopupPosition(event, popupWidth, popupHeight);
}

/**
 * In mobile Firefox, the popup is rendered below the selection in the center
 * of the screen.
 */
function calculateMobileHighlightPopupPosition(
  selectedRange: Range,
  popupWidth: number
): { top: number; left: number } {
  const rangeBoundaries = selectedRange.getBoundingClientRect();
  const top = rangeBoundaries.bottom + window.scrollY + 20;
  const left = window.innerWidth / 2 - popupWidth / 2;
  return { top, left };
}

/**
 * On desktop, the popup is rendered at the position where the mouseup event
 * occurred.
 * If the popup is rendered outside the window, its position is adjusted
 * so that it is within the window again.
 */
function calculateDesktopHighlightPopupPosition(
  event: MouseEvent,
  popupWidth: number,
  popupHeight: number
): { top: number; left: number } {
  let left = event.clientX;
  // Check if the popup would be rendered outside the right of the window.
  const leftOverlapRight = left + popupWidth - window.innerWidth;
  if (leftOverlapRight > 0) {
    left -= leftOverlapRight + 10;
  }

  // Check if the popup would be rendered outside the left of the window.
  const isOutsideOfWindowLeft = left - popupWidth < 0;
  if (isOutsideOfWindowLeft) {
    left = 10;
  }

  let top = event.clientY;

  // Check if the popup would be rendered outside the bottom of the window.
  const topOverlapBottom = top + popupHeight - window.innerHeight;
  if (topOverlapBottom > 0) {
    top -= topOverlapBottom + 10;
  }

  // Check if the popup would be rendered outside the top of the window.
  const isOutsideOfWindowTop = top < 0;
  if (isOutsideOfWindowTop) {
    top = 10;
  }

  return { top: top + window.scrollY, left: left + window.scrollX };
}

/**
 * This function calculates the boundaries of a set of HTML elements.
 * It returns an object containing the top, bottom, and center coordinates.
 */
export function calculateBoundaries(elements: HTMLElement[]): {
  top: number;
  bottom: number;
  center: number;
} {
  let left = Infinity;
  let right = -Infinity;
  let top = Infinity;
  let bottom = -Infinity;

  for (const element of elements) {
    const rect = element.getBoundingClientRect();

    if (rect.left < left) {
      left = rect.left;
    }

    if (rect.right > right) {
      right = rect.right;
    }

    if (rect.top < top) {
      top = rect.top;
    }

    if (rect.bottom > bottom) {
      bottom = rect.bottom;
    }
  }

  return { top, bottom, center: left + (right - left) / 2 };
}
