import { ReactNode, useEffect, useState } from 'react';
import { IconX } from '@tabler/icons-react';

export type ToastProps = {
  title: string;
  description: string;
};

/**
 * Component for a toast notification in the content script.
 */
export function Toast({ title, description }: ToastProps): ReactNode {
  const [visible, setVisible] = useState(true);

  const onClose = () => {
    setVisible(false);
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      setVisible(false);
    }, 5000);

    return () => clearTimeout(timer);
  }, []);

  return (
    <div id="toast" className={visible ? 'show' : undefined}>
      <div>
        <h3>{title}</h3>
        <p>{description}</p>
      </div>
      <button className="close-button" onClick={onClose} type="button">
        <IconX size={16} />
      </button>
    </div>
  );
}
