import { MouseEvent, ReactElement, ReactNode, TouchEvent } from 'react';
import { isMobileDevice } from '@utils/mobile';

type ActionProps = {
  children: ReactNode;
  onClick?: () => void;
  as?: 'div' | 'button';
  className?: string;
};

/** A single action for highlighting.*/
export function Action({
  as = 'button',
  onClick,
  className,
  children,
}: ActionProps): ReactElement {
  const withStopPropagation = (event: MouseEvent | TouchEvent) => {
    event.stopPropagation();
    onClick?.();
  };

  const isMobile = isMobileDevice();

  const Component = as ?? 'span';

  return (
    <Component
      onClick={isMobile ? (e) => e.stopPropagation() : withStopPropagation}
      onTouchStart={withStopPropagation}
      className={`action ${className ? className : ''}`}
    >
      {children}
    </Component>
  );
}
