import { Highlight } from '@custom-types/highlight';
import { Message, MessageType } from '@custom-types/message';
import Browser from 'webextension-polyfill';

/** Retrieve a highlight from the background. */
export function fetchHighlight(highlightID: string): Promise<Highlight> {
  const getHighlightMessage: Message = {
    type: MessageType.GetHighlight,
    payload: highlightID,
  };

  return Browser.runtime.sendMessage(getHighlightMessage);
}

/**
 * Send a message to the background to update the highlight.
 * Triggers a refetch after updating the highlight.
 */
export async function updateHighlight(
  updatedHighlight: Highlight
): Promise<void> {
  const updateHighlightMessage: Message = {
    type: MessageType.UpdateHighlight,
    payload: updatedHighlight,
  };
  await Browser.runtime.sendMessage(updateHighlightMessage);
  return triggerHighlightRefetch();
}

/**
 * Store a highlight in the DB via the background.
 * Triggers a refetch after storing the highlight
 */
export async function storeHighlight(highlight: Highlight): Promise<void> {
  await Browser.runtime.sendMessage({
    type: MessageType.StoreHighlightInDB,
    payload: highlight,
  });
  return triggerHighlightRefetch();
}

/** Inform the sidebar that highlights should be refetched. */
export function triggerHighlightRefetch(): Promise<void> {
  return Browser.runtime.sendMessage({ type: MessageType.RefetchHighlights });
}

/**
 * Delete a highlight from the DB via the background.
 * Triggers a refetch after deleting the highlight
 */
export async function deleteHighlight(highlightID: string): Promise<void> {
  const message: Message = {
    type: MessageType.DeleteFromStore,
    payload: highlightID,
  };
  await Browser.runtime.sendMessage(message);
  return triggerHighlightRefetch();
}
